<template>
	<div>
	<ol class="breadcrumb">
	    <li><a href="javascript:;"></a></li>
	    <li class="active"></li>
	</ol>
		
		<div class="panel panel-info">
		    <div class="panel-heading">		    
		        <h3 class="panel-title">
		            
		            <router-link class="pull-right addBtn" :to="{name:'addPre'}"><i class="glyphicon glyphicon-plus"></i></router-link>
		        </h3>
		    </div>
		    <div class="panel-body">

		        <div class="table-responsive" v-if="list.length>0">
				  <table class="table table-condensed table-bordered table-striped">
				    <thead>
				      <tr>
				        <th></th>
				        <th></th>
				        <th></th>
				        <!-- <th></th> -->
				        <!-- <th></th> -->
				        <th></th>
				        <th></th>
				        <th></th>
				        <th style="min-width:220px;"></th>
				        </tr>
				    </thead>
				    <tbody>
				    	<tr v-for="item in list">
				    		<td>{{item.title}}</td>
				    		<td>{{item.shortTitle}}</td>
				    		<td v-if="item.isFrontPage==0" style="text-align: center"></td>
				    		<td v-if="item.isFrontPage==1" style="text-align: center"></td>
				    		<!-- <td><img :src="item.thumbnail" alt=""></td> -->
				    		<!-- <td style="text-align: center;">{{item.hazardClass}}</td> -->
				    		<td>{{item.source}}</td>
				    		<td style="white-space: nowrap">{{item.createDateStr}}</td>
				    		<td style="white-space: nowrap">{{item.updateDateStr}}</td>
				    		<td style="white-space:nowrap"><router-link class="btn btn-default" :to="{name:'updatePre', query: { id:item.id}}" style="margin-right:10px;"></router-link><router-link class="btn btn-primary" :to="{name:'findPre', query: { id:item.id}}" style="margin-right: 10px;"></router-link><a href="javascript:;" class="btn btn-danger" @click="removeFn(item.id)" style="margin-right: 10px;"></a><a :href="item.snapshot" target="_blank" class="btn btn-info" v-if="item.snapshot"></a></td>				    		
				    	</tr>
				      
				    </tbody>
				  </table>
				</div>
				<div style="text-align: center" v-else></div>
				<div id="Pagination" class="pagination"></div>
			</div>
		</div>

		

	</div>
	
</template>
<script>
export default {
  name: 'app',
  data () {
    return {
      api:{
      	getList:"/genuine/server_preference/serverList",
      	remove:"/genuine/server_preference/serverDelete"
      },
      list:[],
      count:0,
      isInitPage:false
    }
  },
  mounted(){
  	
  },
  created(){
  	this.getList(1,10);

  	
  },
  methods:{
  	getList:function(pageNo,pageSize){
  		var self=this;
  		this.axios.get(self.api.getList, {
		params: {pageNo:pageNo,pageSize:pageSize}
		}).then(function (response) {
		if(response.data.errorCode=="0"){
            		self.list = response.data.result.list;
           			self.count=Math.ceil(response.data.result.count/10);
           			if(!self.isInitPage){
           				self.initPage();
           			}
           			                 		
                
            	}else{
            		zdalert('',response.data.reason,function(){});   
            	}            
		}).catch(function (error) {
		// zdalert('',error,function(){});   
		});
  	},
  	initPage:function(){
	  	var self=this;	
	  	self.isInitPage=true
	  	var initPagination = function() {
			// 
			$("#Pagination").pagination(self.count, {
				num_edge_entries: 1, //
				num_display_entries: 4, //
				callback: pageselectCallback,
				items_per_page: 1, //1
				prev_text: "",
				next_text: ""
			});
		 };
		 initPagination()
		function pageselectCallback(page_index, jq){
			self.getList(page_index+1,10)			
			return false;
		} 
		
	  },
	  removeFn:function(id){
	  	var self=this;
	  	zdconfirm('','',function(r){  
		     if(r)  
		      {  
		         self.axios.get(self.api.remove, {
					params: {id:id}
					}).then(function (response) {
					if(response.data.errorCode=="0"){
						zdalert('',"",function(){
							location.reload()  
						}); 
								   		
			                
			            	}else{
			            		zdalert('',response.data.reason,function(){}); 
			            	}            
					}).catch(function (error) {
					// zdalert('',error,function(){}); 
					});
		      }  
		    }); 
	  }

  }
  
}
</script>

<style lang="scss">
.addBtn{color: #337ab7!important}
.addBtn i{margin-right: 5px;}
</style>


